import React from 'react';
import { Card, Row, Col } from 'antd';

const Home = () => {
    const dummy = [
        {
            title: "DSM공모전",
            admin: "김주성",
            body: "공모전을 위한 클래스"
        }
    ]
    return (
        <div>
            <Row gutter={16}>
                {
                    dummy.map(v => {
                        return (
                            <Col span={4}>
                                <Card style={{width:300}} title={v.title}>{v.body}</Card>
                            </Col>
                        )
                    })
                }
            </Row>
            
        </div>
    )
}

export default Home;